import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'
import VueScrollTo from 'vue-scrollto'
import VueClipboard from 'vue-clipboard2'

import hljs from 'highlight.js/lib/core'
import 'highlight.js/styles/atom-one-dark.css'
import javascript from 'highlight.js/lib/languages/javascript'
import json from 'highlight.js/lib/languages/json'
import go from 'highlight.js/lib/languages/go'
import shell from 'highlight.js/lib/languages/shell'
import java from 'highlight.js/lib/languages/java'
import python from 'highlight.js/lib/languages/python'

import Element from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import store from './store'
import routes from './router'
import Main from './Main.vue'
import { CODE_LANG_KEY } from './utils'

hljs.registerLanguage(CODE_LANG_KEY.js, javascript)
hljs.registerLanguage('json', json)
hljs.registerLanguage(CODE_LANG_KEY.go, go)
hljs.registerLanguage(CODE_LANG_KEY.shell, shell)
hljs.registerLanguage(CODE_LANG_KEY.java, java)
hljs.registerLanguage(CODE_LANG_KEY.python, python)

Vue.use(VueScrollTo, {
  container: "#scroll-container",
  duration: 500,
  easing: "ease",
  offset: 0,
  force: true,
})
Vue.use(Element)
Vue.use(VueClipboard)
Vue.use(Router)

Vue.prototype.$axios = axios
Vue.config.productionTip = false
Vue.prototype.$hljs = hljs

const router =  new Router({
	mode: 'hash',
  routes: routes,
})

new Vue({
  el: '#app',
  router,
  store,
  render: h => h(Main),
})



