const CACHE_KEY = 'lang'

export const LANG_KEY = {
  en: 'en-US',
  zh: 'zh-hans',
  tw: 'zh-tw',
}

export const CODE_LANG_KEY = {
  js: 'javascript',
  go: 'golang',
  shell: 'shell',
  java: 'java',
  python: 'python',
}

export const codeLangConfig = [{
  label: 'Golang',
  value: CODE_LANG_KEY.go,
}, {
  label: 'Nodejs',
  value: CODE_LANG_KEY.js,
}, {
  label: 'Shell',
  value: CODE_LANG_KEY.shell,
}, {
  label: 'Java',
  value: CODE_LANG_KEY.java,
},{
  label: 'Python',
  value: CODE_LANG_KEY.python,
}]

export const defaultLangKey = 'en'
// 支持的语言
export const LANGS = [{
  label: 'English',
  value: LANG_KEY.en,
}, {
  label: '简体中文',
  value: LANG_KEY.zh,
}, {
  label: '繁体中文',
  value: LANG_KEY.tw,
}]

export const getLang = () => {
  return window.localStorage.getItem(CACHE_KEY) || LANG_KEY[defaultLangKey]
}

export const setLang = (lang) => {
  window.localStorage.setItem(CACHE_KEY, lang)
}

export const getLangKeyByLang = (lang) => {
  let key = defaultLangKey
  Object.keys(item => {
    if(LANG_KEY[item] === lang) {
      key = item
    }
  })
  return key
}



