
//通用
import { Data as AuthContent } from '../authentication/index'
import { Data as ChangeLogContent } from '../changelog/index'
import { Data as OtherContent } from '../listenKey/index'

//共用
import { List as SocketContent } from '../spot/websocket/index'
import { Data as BasicContent } from '../spot/api/basic/index'
import { Data as AccountContent } from '../spot/api/account/index'

import Introduce from './1_introduce.json'
import { Data as PermissionContent } from './api/permission/index'



let aaa = JSON.stringify(OtherContent)
aaa = aaa.replaceAll("/market/<listenKey>", "/market?listenKey==")
aaa = aaa.replaceAll("wss://open-api-ws.bingx.com/market?listenKey=", "wss://open-api-ws.bingx.com/market?listenKey=a8ea75681542e66f1a50a1616dd06ed77dab61baa0c296bca03a9b13ee5f2dd7")

const ROOT_MODULE = "common"

export const Socket = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/socket`,
  "name": "Socket 文档",
  "children": SocketContent
}


const ChangeLog = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/changelog`,
  "name": "变更日志",
  "content": ChangeLogContent
}

const Authentication = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/authentication.html`,
  "name": "签名认证",
  "content": AuthContent
}
const Basic = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/base-info.html`,
  "name": "基础信息",
  "content": BasicContent
}
const Account = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/account-api.html`,
  "name": "用户划转和充值和提币接口",
  "content": AccountContent
}
const Permission = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/permission-interface.html`,
  "name": "权限接口",
  "content": PermissionContent
}
const Auth = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/authentication-interface.html`,
  "name": "鉴权服务",
  "content": JSON.parse(aaa)
}


export const commonConfig = [
  Introduce,
  ChangeLog,
  Basic,
  Authentication,
  Permission,
  Account,
  Auth,
]


