<template>
  <div style="height: 100%;">
    <NavHeader :lang="currentLang" />
    <router-view :lang="currentLang" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

import NavHeader from './components/NavHeader'

export default {
  components: {
    NavHeader
  },

  computed: {
    ...mapGetters(['currentLang'])
  },
}
</script>


