//通用
import { Data as ChangeLogContent } from '../changelog/index'
import { Data as OtherContent } from '../listenKey/index'

//共用
import { Data as BasicContent } from '../swapV2/api/basic/index_share_to_v1'

import Introduce from './1_introduce.json'
import { Data as AuthContent } from './api/authentication/index'
import { Data as AccountContent } from './api/account/index'
import { Data as TradeOrderContent } from './api/trade/index'
import { Data as MarketContent } from './api/market/index'
import system_time from './api/basic/05_system_time.json'
import { List as SocketContent } from './websocket/index'
const ROOT_MODULE = "swap"

export const Socket = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/socket`,
  "name": "Socket 文檔",
  "children": SocketContent
}


const ChangeLog = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/changelog`,
  "name": "變更日誌",
  "content": ChangeLogContent
}
const TradeOrder = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/trade-api.html`,
  "name": "交易接口",
  "content": TradeOrderContent
}
const Authentication = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/authentication.html`,
  "name": "簽名認證",
  "content": AuthContent
}
const Basic = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/base-info.html`,
  "name": "基礎信息",
  "content": BasicContent
}

Basic.content = Basic.content.concat(system_time)

const Account = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/account-api.html`,
  "name": "賬戶接口",
  "content": AccountContent
}
const aaa = JSON.stringify(OtherContent)
let bbb = aaa.replaceAll("/openApi/user/auth/userDataStream", "/api/v1/user/auth/userDataStream")
bbb = bbb.replaceAll("open-api.bingx.com", "api-swap-rest.bingbon.pro")
const Other = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/other-interface.html`,
  "name": "其他接口",
  "content": JSON.parse(bbb)
}
const Market = {
  "module": ROOT_MODULE,
  "path": `/${ROOT_MODULE}/market-api.html`,
  "name": "行情接口",
  "content": MarketContent
}

export const swapConfig = [
  Introduce,
  ChangeLog,
  Authentication,
  Basic,
  Market,
  Account,
  TradeOrder,
  Other,
  Socket,
]


